import "./styles/app.scss";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { useEffect, useState } from "react";
import PackagePage from "./pages/package/page";
import { SWRConfig } from "swr";
import SearchBar from "./search-bar";
import { Toast } from "bootstrap";
import { dataFetcher } from "./data";

const swrConfig = {
  fetcher: dataFetcher,
  onErrorRetry: (error, key, config, revalidate, { retryCount }) => {
    if (error && error.response && error.response.status === 404) {
      // TODO: Redirect to 404.html
      return;
    }
    if (retryCount >= 5) {
      config.appNotifyError(error);
      return;
    }
    setTimeout(() => revalidate({ retryCount }), config.errorRetryInterval);
  },
};

export default function App() {
  useEffect(() => {
    const toastElList = [].slice.call(document.querySelectorAll(".toast"));
    toastElList.forEach((item) => new Toast(item).show());
  });
  const [swrError, setSwrError] = useState(null);
  const routes = (
    <Routes>
      <Route
        path="/packages/:type/:ownername/:name/*"
        element={<PackagePage />}
      />
    </Routes>
  );
  return (
    <SWRConfig
      value={{
        ...swrConfig,
        ...{ appNotifyError: setSwrError },
      }}
    >
      <BrowserRouter>
        <header className="p-4 shadow border border-bottom">
          <SearchBar />
        </header>
        <main className="container py-4">{routes}</main>
      </BrowserRouter>
      {swrError ? renderError(swrError) : ""}
    </SWRConfig>
  );
}

function renderError(error) {
  return (
    <div
      className="toast position-fixed top-0 end-0 align-items-center text-white bg-danger border-0 m-2"
      style={{ zIndex: 50 }}
      role="alert"
      aria-live="assertive"
      aria-atomic="true"
      data-bs-autohide="false"
    >
      <div className="d-flex">
        <div className="toast-body">{error.toString()}</div>
        <button
          type="button"
          className="btn-close btn-close-white me-2 m-auto"
          data-bs-dismiss="toast"
          aria-label="Close"
        ></button>
      </div>
    </div>
  );
}
