import { Link, useParams, useSearchParams } from "react-router-dom";
import { CopyToClipboard } from "react-copy-to-clipboard";
import Head from "../../components/head";
import Humanize from "../../components/humanize";
import Icon from "../../components/icon";
import PackageTabs from "./tabs";
import { REGISTRY_FRONTEND_ENDPOINT } from "../../constants";
import Spinner from "../../components/spinner";
import { Tooltip } from "bootstrap";
import Truncate from "../../components/truncate";
import qs from "querystringify";
import { useEffect } from "react";
import { usePackageData } from "../../data";

export default function PackagePage() {
  useEffect(() => {
    const tooltipTriggerList = [].slice.call(
      document.querySelectorAll('[data-bs-toggle="tooltip"]')
    );
    tooltipTriggerList.forEach((item) => new Tooltip(item));
    window.scrollTo(0, 0);
  });

  const params = useParams();
  const [searchParams] = useSearchParams();
  const customVersion = searchParams.get("version");
  const { data, isLoading } = usePackageData({
    ...params,
    version: customVersion,
  });
  if (isLoading) return <Spinner />;

  const makeSelfUrl = ({ extraPath, customVersion, resolve } = {}) =>
    makePackageUrl({
      type: data.type,
      ownername: data.owner.username,
      name: data.name,
      extraPath,
      resolve,
      version:
        customVersion || customVersion === null
          ? customVersion
          : !data.version.is_latest
          ? data.version.name
          : undefined,
    });

  const byOwner = getByOwner(data.owner);
  return (
    <>
      <Head
        title={data.name + " by " + byOwner}
        description={data.description}
        noindex={!!customVersion}
      />
      <div className="hstack flex-wrap gap-2">
        <div>
          <h1 className="h2">
            <CopyToClipboard text={`${data.owner.username}/${data.name}`}>
              <span
                role="button"
                data-bs-toggle="tooltip"
                title="Click to Copy Dependency"
              >
                {data.owner.username}/{data.name}
              </span>
            </CopyToClipboard>
            <small className="h5 ms-2 me-2">
              <span className="text-muted me-1">by</span>
              <Link
                to={`/search?${qs.stringify({
                  query: `owner:"${data.owner.username}"`,
                })}`}
              >
                {byOwner}
              </Link>
            </small>
          </h1>
        </div>
        <div>
          <sup className="h5">
            <span className="badge bg-secondary text-uppercase me-2">
              <div className="d-flex flex-row align-items-center">
                <div>
                  <Icon name={`package-${data.type}`} />
                </div>
                <div className="ps-2">{data.type}</div>
              </div>
            </span>
            {/* <span className="badge bg-success me-2">
              <div className="d-flex flex-row align-items-center">
                <div>
                  <Icon name="shield-check" />
                </div>
                <div className="ps-2">OFFICIAL</div>
              </div>
            </span> */}
            {data.popularity_rank <= 100 ? (
              <span className="badge bg-warning me-2">
                <div className="d-flex flex-row align-items-center">
                  <div>
                    <Icon name="flame" />
                  </div>
                  <div className="ps-2">TOP 100</div>
                </div>
              </span>
            ) : (
              ""
            )}
          </sup>
        </div>
      </div>

      <ul className="list-inline text-muted">
        <li className="list-inline-item">
          <Humanize type="original-version" value={data.version.name} />
        </li>
        <li className="list-inline-item">â€¢</li>
        <li className="list-inline-item text-success">Public</li>
        <li className="list-inline-item">â€¢</li>
        <li className="list-inline-item">
          Published <Humanize type="time" value={data.version.released_at} />
        </li>
        {data.keywords ? (
          <>
            <li className="list-inline-item">â€¢</li>
            <li className="list-inline-item">
              <Truncate
                value={data.keywords.map((item) => (
                  <Link
                    key={item}
                    to={`/search?${qs.stringify({
                      query: `keyword:"${item}"`,
                    })}`}
                    className="me-2"
                  >
                    <span className="badge rounded-pill border border-primary text-primary">
                      {item}
                    </span>
                  </Link>
                ))}
                max={5}
                moreTitle={`+ ${data.keywords.length - 5} categories`}
              />
            </li>
          </>
        ) : (
          ""
        )}
      </ul>
      <p className="mb-4">
        <Truncate value={data.description} max={255} moreTitle="Read more â†’" />
      </p>
      <PackageTabs
        data={data}
        leftoverPath={params["*"] || undefined}
        makeSelfUrl={makeSelfUrl.bind()}
      />
    </>
  );
}

function getByOwner(owner) {
  let by = owner.firstname || "";
  if (owner.lastname) {
    by += (by ? " " : "") + owner.lastname;
  }
  return by || owner.username;
}

export function makePackageUrl({
  type,
  ownername,
  name,
  extraPath,
  version,
  resolve,
}) {
  const parts = resolve ? [REGISTRY_FRONTEND_ENDPOINT] : [""];
  parts.push("packages");
  switch (type) {
    case "library":
      parts.push("libraries");
      break;
    case "platform":
      parts.push("platform");
      break;
    case "tool":
      parts.push("tools");
      break;
    default:
      parts.push(type);
  }
  parts.push(...[ownername, name]);
  if (extraPath) {
    parts.push(extraPath);
  }
  return version ? `${parts.join("/")}?version=${version}` : parts.join("/");
}
