import PropTypes from "prop-types";
import ellipsize from "ellipsize";
import { useState } from "react";

function Truncate(props) {
  const [expand, setExpand] = useState(false);
  if (!props.value || props.value.length <= props.max) {
    return props.value;
  }
  const btn = (
    <button
      type="button"
      className="btn btn-link btn-sm btn-truncate"
      onClick={() => setExpand(!expand)}
    >
      {expand ? props.lessTitle : props.moreTitle}
    </button>
  );
  return (
    <>
      {expand
        ? props.value
        : props.value instanceof Array
        ? props.value.slice(0, props.max)
        : truncateText(props.value, props.max)}
      {btn}
    </>
  );
}

function truncateText(text, max) {
  if (text.length < max) return text;
  text = ellipsize(text, max, { ellipse: "" });
  while ([".", ",", ";"].includes(text[text.length - 1])) {
    text = text.substring(0, text.length - 1);
  }
  return text + "...";
}

Truncate.defaultProps = {
  lessTitle: "Show less",
  moreTitle: "Show more",
};

Truncate.propTypes = {
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.array]).isRequired,
  max: PropTypes.number.isRequired,
  lessTitle: PropTypes.string,
  moreTitle: PropTypes.string,
};

export default Truncate;
