import Humanize from "../../components/humanize";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Shield from "../../components/shield";

function PackageSidebar(props) {
  const versionsUrl = props.makeSelfUrl({
    extraPath: "versions",
  });
  return (
    <aside className="package-sidebar">
      <ul className="list-group list-group-flush">
        {props.data.homepage ? (
          <li className="list-group-item">
            <h3>Homepage</h3>
            <Icon name="link" />
            <a
              href={props.data.homepage}
              className="text-small ms-2"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Humanize type="url" value={props.data.homepage} />
            </a>
          </li>
        ) : (
          ""
        )}
        {props.data.repository_url ? (
          <li className="list-group-item">
            <h3>Repository</h3>
            <Icon
              name={
                props.data.repository_url.includes("https://github.com")
                  ? "mark-github"
                  : "repo-forked"
              }
            />
            <a
              href={props.data.repository_url}
              className="text-small ms-2"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Humanize type="url" value={props.data.repository_url} />
            </a>
          </li>
        ) : (
          ""
        )}
        <li className="list-group-item">
          <div className="row">
            <div className="col-6">
              <h3>Popularity</h3>
              <div className="hstack align-items-start gap-2 fs-6">
                <div
                  className={
                    props.data.popularity_trend < 0
                      ? "text-danger"
                      : "text-success"
                  }
                >
                  <Icon
                    name={
                      props.data.popularity_trend < 0
                        ? "chart-down"
                        : "chart-up"
                    }
                  />
                </div>
                <div>
                  <a href="#">
                    <Humanize
                      type="counter"
                      value={props.data.popularity_rank}
                    />
                  </a>
                </div>
                <div className="text-muted popularity">
                  <small>
                    <Icon
                      name={
                        props.data.popularity_trend < 0
                          ? "triangle-down"
                          : "triangle-up"
                      }
                    />
                    {Math.abs(props.data.popularity_trend)}
                  </small>
                </div>
              </div>
            </div>
            <div className="col-6">
              <h3>License</h3>
              {props.data.license ? (
                <a
                  href={`https://spdx.org/licenses/${props.data.license}.html`}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {props.data.license}
                </a>
              ) : (
                "Unknown"
              )}
            </div>
          </div>
        </li>
        {props.data.stars_count > 0 || props.data.open_issues_count > 0 ? (
          <li className="list-group-item">
            <div className="row">
              <div className="col-6">
                <h3>Stars</h3>
                <Icon name="star-fill" />
                <span className="ms-2">
                  <Humanize type="counter" value={props.data.stars_count} />
                </span>
              </div>
              <div className="col-6">
                <h3>Issues</h3>
                <Humanize type="counter" value={props.data.open_issues_count} />
              </div>
            </div>
          </li>
        ) : (
          ""
        )}
        <li className="list-group-item">
          <div className="row">
            <div className="col-6">
              <h3>Last Modified</h3>
              <Humanize type="time" value={props.data.modified_at} />
            </div>
            <div className="col-6">
              <h3>Unpacked Size</h3>
              <Humanize type="size" value={props.data.version.unpacked_size} />
            </div>
          </div>
        </li>
        <li className="list-group-item py-3">
          <h3>
            <Link to={versionsUrl} className="link-secondary">
              Versions
            </Link>{" "}
            <span className="badge rounded-pill bg-gray-200 text-dark">
              {props.data.versions.length}
            </span>
          </h3>
          <div className="hstack align-items-start gap-2">
            <div
              className={
                props.data.version.is_latest ? "text-success" : "text-danger"
              }
            >
              <Icon name="tag" />
            </div>
            <div>
              <Link to={versionsUrl}>{props.data.version.name}</Link>
            </div>
            {props.data.version.is_latest ? (
              <div>
                <span className="badge rounded-pill border border-success text-success">
                  Latest
                </span>
              </div>
            ) : (
              <div>
                <span className="badge rounded-pill border border-danger text-danger">
                  Newer version available
                </span>
              </div>
            )}
          </div>
          <div className="text-muted ms-4">
            <small>
              <Humanize type="time" value={props.data.version.released_at} />
            </small>
          </div>
          {props.data.versions.length > 1 ? (
            <div className="mt-2">
              <Link to={versionsUrl} className="text-small">{`+ ${
                props.data.versions.length - 1
              } versions`}</Link>
            </div>
          ) : (
            ""
          )}
        </li>
        <li className="list-group-item py-3">
          <h3>Badge</h3>
          <Shield
            title="PlatformIO Package Registry"
            message={`v${props.data.version.name}`}
            href={props.makeSelfUrl({
              resolve: true,
            })}
          />
        </li>
      </ul>
    </aside>
  );
}

PackageSidebar.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
    modified_at: PropTypes.string.isRequired,
    version: PropTypes.shape({
      name: PropTypes.string.isRequired,
      released_at: PropTypes.string.isRequired,
      unpacked_size: PropTypes.number.isRequired,
      is_latest: PropTypes.bool.isRequired,
    }).isRequired,
    versions: PropTypes.array.isRequired,
    popularity_rank: PropTypes.number.isRequired,
    popularity_trend: PropTypes.number.isRequired,
    homepage: PropTypes.string,
    repository_url: PropTypes.string,
    license: PropTypes.string,
    stars_count: PropTypes.number,
    open_issues_count: PropTypes.number,
    keywords: PropTypes.arrayOf(PropTypes.string),
  }).isRequired,
  makeSelfUrl: PropTypes.func.isRequired,
};

export default PackageSidebar;
