import { HumanizeCode } from "../../components/humanize";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Truncate from "../../components/truncate";

function PackageInstallation(props) {
  let requirements = undefined;
  if (props.data.version.is_latest) {
    if (/^[\d.]+$/.test(props.data.version.name)) {
      requirements = "^" + props.data.version.name;
    }
  } else {
    requirements = props.data.version.name;
  }
  const deps = `${props.data.owner.username}/${props.data.name}${
    requirements ? `@${requirements}` : ""
  }`;
  return (
    <div className="installation readme-contents">
      {renderQuickStartGuide(props.data.type, deps)}
      {renderUsageGuide(props.data, props.makeSelfUrl)}
      {renderCLIGuide(props.data.type, deps)}
    </div>
  );
}

function renderQuickStartGuide(type, deps) {
  let option = undefined;
  let optionDocsUrl = undefined;
  switch (type) {
    case "library":
      option = "lib_deps";
      optionDocsUrl =
        "https://docs.platformio.org/en/latest/projectconf/section_env_library.html#lib-deps";
      break;
    case "platform":
      option = "platform";
      optionDocsUrl =
        "https://docs.platformio.org/en/latest/projectconf/section_env_platform.html";
      break;
    case "tool":
      option = "platform_packages";
      optionDocsUrl =
        "https://docs.platformio.org/en/latest/projectconf/section_env_platform.html#platform-packages";
      break;
    default:
      return;
  }
  return (
    <>
      {type === "tool" ? (
        <div className="alert alert-warning" role="alert">
          You do not need to install tools manually, they are part of{" "}
          <a href="https://docs.platformio.org/en/latest/platforms/index.html">
            Development Platforms
          </a>
          . If you want to override a platform-dependent tool with another
          version, see the instruction below.
        </div>
      ) : (
        ""
      )}
      <h2>Quick Start</h2>
      <ol className="mb-4">
        <li className="pb-1">
          Open{" "}
          <a href="https://docs.platformio.org/en/latest/projectconf/index.html">
            platformio.ini
          </a>
          , a project configuration file located in the root of PlatformIO
          project.
        </li>
        <li>
          Add the following <a href={optionDocsUrl}>{option}</a> option to your{" "}
          <code>[env:]</code> section:
          <div className="py-1 user-select-all">
            <HumanizeCode code={`${option} = ${deps}`} lang="cpp" />
          </div>
        </li>
        <li>
          Build a project, PlatformIO will automatically install dependencies.
        </li>
      </ol>
    </>
  );
}

function renderUsageGuide(data, makeSelfUrl) {
  const examplesUrl = makeSelfUrl({
    extraPath: "examples",
  });
  let guide = undefined;
  if (data.type === "library" && (data.headers || []).length) {
    guide = renderLibraryUsageGuide(data.name, data.headers, examplesUrl);
  } else if (data.type === "tool") {
    guide = renderToolUsageGuide(data.name);
  }

  return (
    <div className="mb-4">
      <h2>Usage</h2>
      {guide || (
        <>
          Please check <Link to={examplesUrl}>Examples</Link> on how to use this
          package.
        </>
      )}
    </div>
  );
}

function renderLibraryUsageGuide(name, headers, examplesUrl) {
  if (headers.length === 1) {
    return (
      <ol>
        <li className="pb-1">
          Add the following line to your source code:
          <div className="my-1 user-select-all">
            <HumanizeCode code={`#include <${headers[0]}>`} lang="cpp" />
          </div>
        </li>
        <li>
          Check <Link to={examplesUrl}>Examples</Link> on how to use the{" "}
          <b>{name}</b> library.
        </li>
      </ol>
    );
  }
  return (
    <>
      The <b>{name}</b> library provides the next header files that can be
      included in your project:
      <ul className="list-inline headers-list">
        <Truncate
          value={headers.map((item, index) => (
            <li className="list-inline-item">
              <code className="user-select-all">{item}</code>
            </li>
          ))}
          max={5}
          moreTitle={`+ ${headers.length - 5} headers`}
        />
      </ul>
      <div className="my-1 user-select-all">
        <HumanizeCode code={`#include <${headers[0]}>`} lang="cpp" />
      </div>
    </>
  );
}

function renderToolUsageGuide(name) {
  return (
    <>
      The <b>{name}</b> will be installed to the{" "}
      <a href="https://docs.platformio.org/en/latest/projectconf/section_platformio.html#packages-dir">
        package storage
      </a>{" "}
      on your machine. You can later refer to the <b>{name}</b> folder in the{" "}
      <a href="https://docs.platformio.org/en/latest/projectconf/index.html">
        platformio.ini
      </a>{" "}
      file using the{" "}
      <code>
        {/* eslint-disable-next-line no-template-curly-in-string */}
        {"${platformio.package_dir}"}
      </code>{" "}
      interpolation. <br />
      See examples with{" "}
      <a href="https://docs.platformio.org/en/latest/projectconf/section_env_upload.html#upload-command">
        upload_command
      </a>{" "}
      and{" "}
      <a href="https://docs.platformio.org/en/latest/projectconf/section_env_debug.html#debug-server">
        debug_server
      </a>
      .
    </>
  );
}

function renderCLIGuide(type, deps) {
  let cmd = undefined;
  let cmdDocsUrl = undefined;
  switch (type) {
    case "library":
      cmd = "pio lib install";
      cmdDocsUrl =
        "https://docs.platformio.org/en/latest/core/userguide/lib/cmd_install.html";
      break;
    case "platform":
      cmd = "pio platform install";
      cmdDocsUrl =
        "https://docs.platformio.org/en/latest/core/userguide/platforms/cmd_install.html";
      break;
    default:
      return;
  }
  return (
    <>
      <h2>Command Line Interface</h2>
      <ol className="mb-4">
        <li className="pb-1">
          Open{" "}
          <a href="https://docs.platformio.org/en/latest/core/index.html">
            PlatformIO Core CLI
          </a>
          .
        </li>
        {type === "library" ? (
          <li className="pb-1">
            Change directory (<code>cd</code>) to the PlatformIO project where{" "}
            <a href="https://docs.platformio.org/en/latest/projectconf/index.html">
              platformio.ini
            </a>{" "}
            is located.
          </li>
        ) : (
          ""
        )}
        <li>
          Copy the following <a href={cmdDocsUrl}>{cmd}</a> command and paste
          into the CLI shell, press <kbd>Enter</kbd>:
          <div className="my-1 user-select-all">
            <HumanizeCode code={`${cmd} "${deps}"`} lang="cpp" />
          </div>
        </li>
      </ol>
    </>
  );
}

PackageInstallation.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
    headers: PropTypes.arrayOf(PropTypes.string.isRequired),
    version: PropTypes.shape({
      name: PropTypes.string.isRequired,
      is_latest: PropTypes.bool.isRequired,
    }).isRequired,
  }).isRequired,
  makeSelfUrl: PropTypes.func.isRequired,
};

export default PackageInstallation;
