import Humanize, { HumanizeCode } from "./humanize";
import PropTypes from "prop-types";
import Spinner from "./spinner";
import { useExternalContents } from "../data";

function FilePreview(props) {
  const { contents } = useExternalContents(props.url);
  if (!contents) {
    return <Spinner />;
  }
  const fileName = props.url
    .substring(props.url.lastIndexOf("/") + 1)
    .toLowerCase();
  const fileExt = fileName
    .substring(fileName.lastIndexOf(".") + 1)
    .toLowerCase();

  if (fileExt === "md") {
    return (
      <div className="readme-contents">
        <Humanize type="markdown" value={contents} />
      </div>
    );
  }
  return <div className="file-contents">{renderCode(contents, fileExt)}</div>;
}

function renderCode(contents, lang) {
  if (["c", "h", "hpp", "ino", "pde"].includes(lang)) {
    lang = "cpp";
  }
  return <HumanizeCode code={contents} lang={lang} />;
}

FilePreview.propTypes = {
  url: PropTypes.string.isRequired,
};

export default FilePreview;
