import { CopyToClipboard } from "react-copy-to-clipboard";
import PropTypes from "prop-types";

function Shield(props) {
  const shieldImgUrl = `https://img.shields.io/badge/PlatformIO-${props.message}-blue?logo=bmw`;
  if (!props.withCode) {
    return <img src={shieldImgUrl} alt={props.title} />;
  }

  return (
    <div className="d-flex justify-content-between flex-wrap">
      <div className="pe-2">
        <img src={shieldImgUrl} alt={props.title} />
      </div>
      <div>
        <div className="d-flex">
          <div>
            <CopyToClipboard
              text={`<a href="${props.href}"><img src="${shieldImgUrl}" alt="${props.title}" /></a>`}
            >
              <button
                type="button"
                className="btn btn-link btn-sm p-0"
                data-bs-toggle="tooltip"
                data-bs-placement="bottom"
                title="Copy HTML code"
              >
                HTML
              </button>
            </CopyToClipboard>
          </div>
          <div className="px-1">â€¢</div>
          <div>
            <CopyToClipboard
              text={`[![${props.title}](${shieldImgUrl})](${props.href})`}
            >
              <button
                type="button"
                className="btn btn-link btn-sm p-0"
                data-bs-toggle="tooltip"
                data-bs-placement="bottom"
                title="Copy Markdown code"
              >
                Markdown
              </button>
            </CopyToClipboard>
          </div>
        </div>
      </div>
    </div>
  );
}

Shield.defaultProps = {
  withCode: true,
};
Shield.propTypes = {
  title: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  href: PropTypes.string,
  withCode: PropTypes.bool,
};

export default Shield;
