import {
  BookIcon,
  CheckCircleIcon,
  CheckIcon,
  CodeIcon,
  CopyIcon,
  DownloadIcon,
  FileDirectoryFillIcon,
  FileIcon,
  FileZipIcon,
  FlameIcon,
  GearIcon,
  GraphIcon,
  LinkIcon,
  MarkGithubIcon,
  RepoForkedIcon,
  ShieldCheckIcon,
  StackIcon,
  StarFillIcon,
  TagIcon,
  TriangleDownIcon,
  TriangleUpIcon,
} from "@primer/octicons-react";
import PropTypes from "prop-types";

function Icon(props) {
  let svgContent = "";
  let svgViewBox = "0 0 24 24";
  let svgWidth = 0;
  switch (props.size) {
    case "small":
      svgWidth = 16;
      break;
    case "medium":
      svgWidth = 32;
      break;
    case "large":
      svgWidth = 64;
      break;
    default:
      svgWidth = 24;
  }
  switch (props.name) {
    case "book":
      // https://primer.style/octicons/
      return <BookIcon size={props.size} />;
    case "download":
      return <DownloadIcon size={props.size} />;
    case "code":
      return <CodeIcon size={props.size} />;
    case "check":
      return <CheckIcon size={props.size} />;
    case "check-circle":
      return <CheckCircleIcon size={props.size} />;
    case "graph":
      return <GraphIcon size={props.size} />;
    case "tag":
      return <TagIcon size={props.size} />;
    case "shield-check":
      return <ShieldCheckIcon size={props.size} />;
    case "flame":
      return <FlameIcon size={props.size} />;
    case "repo-forked":
      return <RepoForkedIcon size={props.size} />;
    case "triangle-up":
      return <TriangleUpIcon size={props.size} />;
    case "triangle-down":
      return <TriangleDownIcon size={props.size} />;
    case "link":
      return <LinkIcon size={props.size} />;
    case "star-fill":
      return <StarFillIcon size={props.size} />;
    case "copy":
      return <CopyIcon size={props.size} />;
    case "mark-github":
      return <MarkGithubIcon size={props.size} />;
    case "file-directory-fill":
      return <FileDirectoryFillIcon size={props.size} />;
    case "file":
      return <FileIcon size={props.size} />;
    case "file-zip":
      return <FileZipIcon size={props.size} />;
    case "package-library":
      return <BookIcon size={props.size} />;
    case "package-platform":
      return <StackIcon size={props.size} />;
    case "package-tool":
      return <GearIcon size={props.size} />;
    case "chart-up":
      // https://www.imdb.com/title/tt0381061/
      svgContent = (
        <path d="M12 2C6.5 2 2 6.5 2 12s4.5 10 10 10 10-4.5 10-10S17.5 2 12 2zm0 18c-2.6 0-5-1.3-6.4-3.3l2.8-3.4 1.3 1.5c.4.4 1 .4 1.3 0l2.9-3.2 1.3 1.4c.3.3.8.1.8-.3V8.5c0-.3-.2-.5-.5-.5h-4c-.4 0-.6.5-.3.8l1.3 1.4-2.2 2.5L9 11.3c-.4-.4-1-.4-1.3 0L4.6 15c-.4-.9-.6-1.9-.6-3 0-4.4 3.6-8 8-8s8 3.6 8 8-3.6 8-8 8z"></path>
      );
      break;

    case "chart-down":
      // https://www.imdb.com/title/tt0381061/
      svgContent = (
        <path d="M12 2C6.5 2 2 6.5 2 12s4.5 10 10 10 10-4.5 10-10S17.5 2 12 2zm0 18c-4.4 0-8-3.6-8-8 0-1.4.4-2.8 1-3.9L8.4 12c.4.4 1 .4 1.4 0l1.4-1.5 2.4 2.6-1.4 1.4c-.3.3-.1.9.4.9h4.3c.3 0 .5-.2.5-.5v-4.3c0-.4-.5-.7-.9-.3L15 11.6l-3.1-3.3c-.4-.4-1-.4-1.4 0L9.2 9.8 6.3 6.4C7.7 4.9 9.7 4 12 4c4.4 0 8 3.6 8 8s-3.6 8-8 8z"></path>
      );
      break;

    default:
      return "Unknown icon";
  }

  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width={svgWidth}
      height={svgWidth}
      viewBox={svgViewBox}
      className="pricon"
    >
      {svgContent}
    </svg>
  );
}

Icon.propTypes = {
  name: PropTypes.string.isRequired,
  size: PropTypes.any,
};

export default Icon;
