import { useParams, useSearchParams } from "react-router-dom";
import FilePreview from "../../components/file-preview";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { REGISTRY_DL_ENDPOINT } from "../../constants";
import Spinner from "../../components/spinner";
import { parseRepositoryUrl } from "./../../helpers";
import qs from "querystringify";
import { usePackageExamples } from "../../data";

function PackageExamples(props) {
  const params = useParams();
  const explorerPath = params["*"] || undefined;
  const [searchParams] = useSearchParams();
  const customVersion = searchParams.get("version");
  const { items, isLoading } = usePackageExamples({
    type: props.data.type,
    ownername: props.data.owner.username,
    name: props.data.name,
    version: customVersion,
  });
  if (isLoading) return <Spinner />;

  if (!items || !items.length) {
    return (
      <div className="card text-center">
        <div className="card-body">
          <h5 className="card-title">No examples</h5>
          <p className="card-text">
            The <b>{props.data.name}</b> package does not provide any examples
            but you can request them.
          </p>
          <a
            href={makeRequestExamplesUrl(props)}
            className="btn btn-primary"
            target="_blank"
            rel="noopener noreferrer"
          >
            Request examples
          </a>
        </div>
      </div>
    );
  }
  let fileUrl = undefined;
  let activeExample = undefined;
  items.forEach((example) => {
    if ((explorerPath + "/").startsWith(example.name + "/")) {
      activeExample = example;
    }
    example.files.forEach((f) =>
      [example.name, f.path].join("/") === explorerPath ? (fileUrl = f.url) : ""
    );
  });
  return (
    <>
      <div className="d-flex flex-row my-2">
        <div className="flex-fill">{renderBreadcrumb(props, explorerPath)}</div>
        {activeExample ? (
          <div>
            <a
              className="btn btn-sm btn-success text-nowrap"
              href={`${REGISTRY_DL_ENDPOINT}/download/${props.data.owner.username}/${props.data.type}/${props.data.name}/${props.data.version.name}/example/${activeExample.name}.zip`}
              role="button"
              data-bs-toggle="tooltip"
              title={`Download "${activeExample.name
                .split("/")
                .slice(-1)}" example`}
            >
              <Icon name="file-zip" /> Download
            </a>
          </div>
        ) : (
          ""
        )}
      </div>
      {fileUrl ? (
        <FilePreview url={fileUrl} />
      ) : (
        renderExplorer(props, items, explorerPath)
      )}
    </>
  );
}

function renderBreadcrumb(props, path) {
  path = `examples${path ? `/${path}` : ""}`;
  const pathComponents = path.split("/");
  return (
    <nav aria-label="breadcrumb">
      <ol className="breadcrumb">
        {pathComponents.map((item, index) =>
          index === pathComponents.length - 1 ? (
            <li key={index} className="breadcrumb-item active">
              {index === 0 ? "Examples" : item}
            </li>
          ) : (
            <li key={index} className="breadcrumb-item">
              <Link
                to={props.makeSelfUrl({
                  extraPath: pathComponents.slice(0, index + 1).join("/"),
                })}
              >
                {index === 0 ? "Examples" : item}
              </Link>
            </li>
          )
        )}
      </ol>
    </nav>
  );
}

function renderExplorer(props, examples, path) {
  const children = listExampleChildren(examples, path);
  const readmeFile = children.find((item) =>
    ["readme", "readme.md", "readme.rst", "readme.txt"].includes(
      item.name.toLowerCase()
    )
  );
  let readmeFileUrl = undefined;
  if (readmeFile) {
    examples.forEach((example) => {
      example.files.forEach((f) =>
        [example.name, f.path].join("/") === readmeFile.path
          ? (readmeFileUrl = f.url)
          : ""
      );
    });
  }
  return (
    <>
      <div className="list-group example-explorer">
        {children.map((item) => (
          <Link
            key={item.path}
            to={props.makeSelfUrl({
              extraPath: `examples/${item.path}`,
            })}
            className="list-group-item list-group-item-action"
          >
            <Icon
              name={item.isFile ? "file" : "file-directory-fill"}
              size="18"
            />
            <span className="ms-2">{item.name}</span>
          </Link>
        ))}
      </div>
      {readmeFileUrl ? (
        <div className="mt-3">
          <FilePreview url={readmeFileUrl} />
        </div>
      ) : (
        ""
      )}
    </>
  );
}

function listExampleChildren(examples, path) {
  path = path || "";
  const level = path ? path.split("/").length : 0;
  const example = examples
    .sort(nameLengthCmp)
    .find((example) => path.startsWith(example.name));
  const children = example
    ? example.files.map((item) => `${example.name}/${item.path}`)
    : examples.map((item) => item.name);

  const computeChildPath = (name) => {
    const childPath = path ? [path, name].join("/") : name;
    if (!example) {
      const _exmp = examples.find((item) => item.name === childPath);
      if (
        _exmp &&
        _exmp.files.length === 1 &&
        !_exmp.files[0].path.includes("/")
      )
        return [childPath, _exmp.files[0].path].join("/");
    }
    return childPath;
  };

  const result = [];
  const visitedNames = [];
  for (const child of children) {
    if (!child.startsWith(path)) {
      continue;
    }
    const name = child.split("/")[level];
    if (visitedNames.includes(name)) {
      continue;
    }
    visitedNames.push(name);
    result.push({
      isFile: child === [path, name].join("/") && example,
      name,
      path: computeChildPath(name),
    });
  }

  return result.sort(foldersAndFilesCmp);
}

function nameLengthCmp(a, b) {
  if (a.name.length < b.name.length) return 1;
  if (a.name.length > b.name.length) return -1;
  return 0;
}

function foldersAndFilesCmp(a, b) {
  if (a.isFile && !b.isFile) return 1;
  if (!a.isFile && b.isFile) return -1;
  if (a.name > b.name) return 1;
  if (a.name < b.name) return -1;
  return 0;
}

function makeRequestExamplesUrl(props) {
  const result = props.data.repository_url || props.data.homepage;
  const parts = parseRepositoryUrl(props.data.repository_url);
  if (!parts) return result;
  const packageUrl = props.makeSelfUrl({
    resolve: true,
  });
  const title = "[FR] Project Examples for PlatformIO Registry";
  const description = `
Hi,

Currently, the [${props.data.type} page](${packageUrl}) in the PlatformIO Registry doesn't contain any quick start projects for developers. Such examples serve both to highlight widespread use cases and to help newcomers to get up and running much more quickly.

Please consider adding a few comprehensive project examples to the PlatformIO Registry.

Useful links:
- ${packageUrl}
- https://docs.platformio.org/en/latest/librarymanager/config.html#examples
`.trim();
  switch (parts.provider) {
    case "github":
      return `https://github.com/${parts.username}/${
        parts.reponame
      }/issues/new?${qs.stringify({
        title,
        body: description,
      })}`;
    case "gitlab":
      return `https://gitlab.com/${parts.username}/${
        parts.reponame
      }/-/issues/new/?${qs.stringify({
        "issue[title]": title,
        "issue[description]": description,
      })}`;
    default:
      return result;
  }
}

PackageExamples.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
    version: PropTypes.shape({
      name: PropTypes.string.isRequired,
    }).isRequired,
    repository_url: PropTypes.string,
    homepage: PropTypes.string,
  }).isRequired,
  makeSelfUrl: PropTypes.func.isRequired,
};

export default PackageExamples;
