import Humanize from "../../components/humanize";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { parseRepositoryUrl } from "./../../helpers";
import semver from "semver";

function PackageVersions(props) {
  const versionToMajor = {};
  const latestMajorVersions = {};
  for (const version of props.data.versions) {
    const coercedVersion = semver.coerce(version.name);
    const majorVersion = semver.major(coercedVersion);
    versionToMajor[version.name] = majorVersion;
    if (
      !latestMajorVersions[majorVersion] ||
      semver.gt(coercedVersion, latestMajorVersions[majorVersion])
    ) {
      latestMajorVersions[majorVersion] = version.name;
    }
  }
  return (
    <>
      <div className="alert alert-info text-center" role="alert">
        All{" "}
        <span className="badge bg-secondary rounded-pill">
          {props.data.versions.length}
        </span>{" "}
        versions of <b>{props.data.name}</b> since{" "}
        <Humanize
          type="time"
          value={props.data.added_at}
          format="MMMM D, YYYY"
        />
        .
      </div>
      <table className="table">
        <thead>
          <tr>
            <th scope="col">Major</th>
            <th scope="col">Version</th>
            <th scope="col">Size</th>
            <th scope="col">Published</th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {props.data.versions.map((version) => (
            <tr
              key={version.name}
              className={
                props.data.version.name === version.name
                  ? `${version.is_latest ? "table-success" : "table-secondary"}`
                  : ""
              }
            >
              <td>
                <span
                  className={`badge rounded-pill py-2 px-3 ${
                    latestMajorVersions[versionToMajor[version.name]] ===
                    version.name
                      ? version.is_latest
                        ? "bg-success"
                        : "bg-secondary"
                      : "text-dark bg-gray-200"
                  } ${version.is_latest ? "fw-bold" : ""}`}
                >
                  {`${versionToMajor[version.name]}.x`}
                </span>
              </td>
              <td>
                <Link
                  to={props.makeSelfUrl({
                    customVersion: !version.is_latest ? version.name : null,
                  })}
                  className={version.is_latest ? "link-success fw-bold" : ""}
                >
                  {version.name}
                  {version.is_latest ? (
                    <span className="badge rounded-pill border border-success text-success ms-3">
                      Latest
                    </span>
                  ) : (
                    ""
                  )}
                </Link>
              </td>
              <td className="text-muted">
                <Humanize type="size" value={version.unpacked_size} />
              </td>
              <td className="text-muted">
                <Humanize type="time" value={version.released_at} />
              </td>
              <td>
                {props.data.repository_url ? (
                  <small>
                    <a
                      href={makeReleaseNotesUrl(
                        props.data.repository_url,
                        version.name
                      )}
                      className="link-secondary"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Release Notes
                    </a>
                  </small>
                ) : (
                  ""
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
}

function makeReleaseNotesUrl(repositoryUrl, version) {
  const parts = parseRepositoryUrl(repositoryUrl);
  if (!parts) return repositoryUrl;
  switch (parts.provider) {
    case "github":
      return `https://github.com/${parts.username}/${parts.reponame}/releases/tag/v${version}`;
    case "gitlab":
      return `https://gitlab.com/${parts.username}/${parts.reponame}/-/tags/v${version}`;
    default:
      return repositoryUrl;
  }
}

PackageVersions.propTypes = {
  data: PropTypes.shape({
    name: PropTypes.string.isRequired,
    added_at: PropTypes.string.isRequired,
    version: PropTypes.shape({
      name: PropTypes.string.isRequired,
    }).isRequired,
    versions: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string.isRequired,
        released_at: PropTypes.string.isRequired,
        is_latest: PropTypes.bool.isRequired,
      })
    ).isRequired,
    repository_url: PropTypes.string,
  }).isRequired,
  makeSelfUrl: PropTypes.func.isRequired,
};

export default PackageVersions;
